import React from 'react'
import Layout from 'Containers/Layout'
import { LinkArrow, Item, ImgWrapper } from 'Components'
import { Container, Row, Col, Typography } from 'reus'
import { css } from '@emotion/core'
import { StaticQuery, graphql } from 'gatsby'
import styled from '@emotion/styled'
import Img from 'gatsby-image'

const HR = () => {
  const Banner = styled.div`
    padding: 80px 0;
  `
  const ContentWrapper = styled.div`
    padding: 80px 0;
  `
  const StyledRow = styled(Row)`
    flex-direction: ${({reverse}) => reverse ? 'row-reverse' : 'row'};
    padding: 40px 0;
    margin-bottom: 70px;
  `
  const FiturPayroll = styled.div`
    max-width: ${({theme}) => theme.breakPoints.md};
    margin: 0 auto;
  `
  return (
    <StaticQuery
      query={graphql`
        query HRQuery {
          attendance: file(relativePath: { eq: "attendance.png" }) {
            childImageSharp {
              fluid(maxWidth: 50) {
                ...GatsbyImageSharpFluid
              }
            }
          }
          employeeProfile: file(relativePath: { eq: "employee-profile.png" }) {
            childImageSharp {
              fluid(maxWidth: 600) {
                ...GatsbyImageSharpFluid
              }
            }
          }
          employeesAddNewEmployee: file(relativePath: { eq: "employees-add-new-employee.png" }) {
            childImageSharp {
              fluid(maxWidth: 600) {
                ...GatsbyImageSharpFluid
              }
            }
          }
          employeesListPage: file(relativePath: { eq: "employees-list-page.png" }) {
            childImageSharp {
              fluid(maxWidth: 600) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <Layout>
            <Container xl gutter={20}>
              <Banner>
                <Row>
                  <Col md={6}>
                    <div>
                      <Typography
                        tag="h2"
                        type="headline"
                        weight="bold"
                        css={theme => css`color: ${theme.color.primary};`}
                      >
                        Managing your employee faster and easier
                      </Typography>
                      <Typography
                        tag="p"
                        type="intro"
                        css={css`margin-bottom: 20px;};`}
                      >
                        With Modana human resources (HR) services, obtaining information becomes a speedy process as all you need to do is type in what you’re searching for. 
                      </Typography>
                      <LinkArrow text="Try Now for Free"/>  
                    </div>
                  </Col>
                  <Col md={6}>
                    <div></div>
                  </Col>
                </Row>
              </Banner>
              <FiturPayroll>
                <Row justify="center">
                  <Col span>
                    <div>
                      <Item
                        title="Easier Employee Management"
                        image={data.attendance}
                      />
                    </div>
                  </Col>
                  <Col span>
                    <div>
                      <Item
                        title="Trackable actions"
                        image={data.attendance}
                      />
                    </div>
                  </Col>
                  <Col span>
                    <div>
                      <Item
                        title="Paperless and Secure"
                        image={data.attendance}
                      />
                    </div>
                  </Col>
                </Row>
              </FiturPayroll>
              <ContentWrapper>
                <StyledRow align="center" gutter={20}>
                  <Col md={6}>
                    <div>
                      <Typography
                        tag="h4"
                        type="manifesto"
                        css={theme => css`color: ${theme.color.primary};`}
                      >
                        Human Resource
                      </Typography>
                      <Typography
                        tag="p"
                        type="body"
                      >
                        With Modana human resources (HR) services, obtaining information becomes a speedy process as all you need to do is type in what you’re searching for. You only need to enter the data once, such as a salary slip, new payroll policy. Any changes made are automatically updated wherever it is assigned without having to manually change it everywhere.
                      </Typography>
                    </div>
                  </Col>
                  <Col md={6}>
                    <ImgWrapper>
                      <Img fluid={data.employeeProfile.childImageSharp.fluid}/>
                    </ImgWrapper>
                  </Col>
                </StyledRow>
                <StyledRow align="center" gutter={20} reverse>
                  <Col md={6}>
                    <div>
                      <Typography
                        tag="h4"
                        type="manifesto"
                        css={theme => css`color: ${theme.color.primary};`}
                      >
                        Trackable actions
                      </Typography>
                      <Typography
                        tag="p"
                        type="body"
                      >
                        Tracking employee requests such as overtime, leaves and view past requests along with their status will be very easy to do. Employers can easily approve or reject requests based on queries requested by users.
                      </Typography>
                    </div>
                  </Col>
                  <Col md={6}>
                    <ImgWrapper>
                      <Img fluid={data.employeesAddNewEmployee.childImageSharp.fluid}/>
                    </ImgWrapper>
                  </Col>
                </StyledRow>
                <StyledRow align="center" gutter={20}>
                  <Col md={6}>
                    <div>
                      <Typography
                        tag="h4"
                        type="manifesto"
                        css={theme => css`color: ${theme.color.primary};`}
                      >
                        Paperless and Secure
                      </Typography>
                      <Typography
                        tag="p"
                        type="body"
                      >
                        Modana HRIS can also store information and company documents which can only be accessed by authorized personnel. This means that HRIS are completely secure and confidential information can be stored without worries, and without big space
                      </Typography>
                    </div>
                  </Col>
                  <Col md={6}>
                    <ImgWrapper>
                      <Img fluid={data.employeesListPage.childImageSharp.fluid}/>
                    </ImgWrapper>
                  </Col>
                </StyledRow>
              </ContentWrapper>
            </Container>
          </Layout>
        )
      }}
    />
  )
}

export default HR